.App {
  font-family: sans-serif;
  text-align: center;
}
.center {
  /* text-align: center; */
  display: block;
  /* justify-content: center; */
  /* align-items: center; */
  margin: auto;
  margin-top: 12%;
}
.heading {
  font-family: "The Nautigal", cursive;
  font-size: 50px;
  text-align: center;
}

.summary {
  font-family: "Orelega One", cursive;
  font-size: 40px;
  text-align: center;
  margin: 0%;
  padding: 0%;
}

section.about {
  padding: 0px;
  border-right: 20px solid white;
  border-left: 20px solid white;
  text-align: center;
}
section.about h1 {
  color: #3a3a3a;
  font-family: "ProximaNovaT";
  font-size: 65px;
  margin-top: 50px;
}
section.about a.pronounce span {
  color: #ababab;
  cursor: help;
  border-bottom: 1px dotted rgb(216, 216, 216);
  letter-spacing: 1px;
}
section.about a.pronounce {
  border-bottom: none;
}
section.about a.pronounce:hover {
  border-bottom: none;
}
section.about p {
  color: #838383;
  margin: 0 auto;
  margin-bottom: 30px;
  font-size: 21px;
}
section.about p a {
  color: #838383;
  border-bottom: 1px solid #e7e7e7;
  padding-bottom: 1px;
  text-decoration: none;
}
section.about p a:hover {
  border-bottom: 1px solid #009ca8;
}

section .resume {
  /* font-family: "Amatic SC", cursive; */
  font-family: "Orelega One", cursive;
  font-size: 20px;
}

section .resume a {
  text-decoration: none;
}
.social-container {
  background: #eee;
  padding: 25px 50px;
  text-align: center;
}
a.social {
  margin: 0 1rem;
  transition: transform 250ms;
  display: inline-block;
}
a.social:hover {
  transform: translateY(-2px);
}
a.twitter {
  color: #49a1eb;
}

a.instagram {
  color: black;
}

footer p {
  text-align: center;
}
footer .mail {
  text-align: center;
}
footer .mail a {
  text-decoration: none;
  color: black;
  font-family: "Amatic SC", cursive;
}
